.chart-tabs {
    width: 100%;
    height: 100%;
    position: relative;
    overflow-x: hidden;
    overflow-y: auto;
}

.chart-tabs__node-count {
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    background-color: #fff;
    border-top: 1px solid #eee;
    border-right: 1px solid #eee;
    font-size: 13px;
    padding: 5px 11px;
}

.chart-tabs__menu {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
}

.chart-tabs__menu__helper {
    position: absolute;
    right: 156px;
    font-size: 14px;
    line-height: 46px;
    color: #eee;
}

.chart-tabs__menu__helper--chart {
    color: #777;
}

.chart-tabs__menu__wrapper {
    background-color: #fff;
    border-bottom: 1px solid #eee;
    border-left: 1px solid #eee;
    height: 47px;
    display: -ms-flexbox;
    display: flex;
    width: 138px;
}

.chart-tabs__menu__item {
    cursor: pointer;
    width: 46px;
    height: 46px;
    display: block;
    background-color: #fff;
    overflow: hidden;
}

.chart-tabs__menu__item__icon {
    display: block;
    -webkit-transform: scale(0.44);
        -ms-transform: scale(0.44);
            transform: scale(0.44);
    -webkit-transform-origin: top left;
        -ms-transform-origin: top left;
            transform-origin: top left;
    background-color: #fff;
    margin: 12px 0 0 12px;
}

.chart_header {
    -ms-flex-direction: column;
        flex-direction: column;
    color: #fff;
    margin-bottom: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: end;
        justify-content: flex-end;
    position: relative;
}

.chart_header_wrapper {
    margin-bottom: 0;
}

.chart_header-left {
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.chart {
    width: 100%;
    height: 240px;
}

.main-chart {
    background: var(--card-background-color);
    width: 100%;
    height: 460px;
    -webkit-box-shadow: 0 16px 30px rgba(0, 0, 0, 0.1);
            box-shadow: 0 16px 30px rgba(0, 0, 0, 0.1);
    margin-bottom: 40px;
}

.main-chart-horizontal {
    height: 360px;
}

svg text {
    fill: var(--chart-meta-color);
    font-size: 12px;
}

.tick line {
    stroke-width: .5;
    stroke: var(--chart-meta-color);
}

.chart__title {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
}



